@import url('https://fonts.googleapis.com/css2?family=REM:wght@500&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=REM:wght@100&display=swap');

@media only screen and (max-width:480px) {
  .circulo-cuadrado{
    width: 50%;
    border-radius: 50%;
  }
  
  .circulo-rectangulo{
    display: inline-block;
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
  }
  
  .circulo-rectangulo img{
    width: auto;
    height: 100%;
    margin-left: -30%;
  }
  
  .nombre-user-foto{
    position: absolute;
    top: 5px;
    left: 35%;
  }
  
  .head-foto{
    margin: 3%;
    position: relative;
  }

  .descripcion{
    text-align: center;
    font-style: oblique;
    max-width: 90%;
  }

  .contenedor-botones{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    max-width: 90%;
  }

  .button{
    /*cambiar margin pa horizontal*/
    margin: 1%;
    height: 50px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-family: 'REM', sans-serif;
    font-weight: 100;
  }
}

@media only screen and (min-width:480px) {
  .circulo-cuadrado{
    width: 50%;
    border-radius: 50%;
  }
  
  .circulo-rectangulo{
    display: inline-block;
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
  }
  
  .circulo-rectangulo img{
    width: auto;
    height: 100%;
    margin-left: -30%;
  }
  
  .nombre-user-foto{
    position: absolute;
    top: 5px;
    left: 46%;
  }
  
  .head-foto{
    margin: 3%;
    position: relative;
  }

  .descripcion{
    text-align: center;
    font-style: oblique;
    max-width: 40%;
  }

  .contenedor-botones{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    max-width: 40%;
  }

  .button{
    /*cambiar margin pa horizontal*/
    margin: 0.1%;
    height: 50px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-family: 'REM', sans-serif;
    font-weight: 100;
  }
}


